<template>
  <div class="container-fluid" v-if="showBlock">
    <ul class="row">
      <li
        v-for="element in elements"
        class="box col-sm-3"
        v-bind:class="{ green: element.hasClass }"
        @click="stopTimer"
      >
        {{ element.elem }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["delay", "boxNumber"],
  data() {
    return {
      showBlock: false,
      timer: null,
      reactionTime: 0,
      elements: [
        { elem: 1, hasClass: false },
        { elem: 2, hasClass: false },
        { elem: 3, hasClass: false },
        { elem: 4, hasClass: false },
        { elem: 5, hasClass: false },
        { elem: 6, hasClass: false },
        { elem: 7, hasClass: false },
        { elem: 8, hasClass: false },
        { elem: 9, hasClass: false },
      ],
    };
  },
  mounted() {
    this.elements[this.boxNumber].hasClass = true;

    setTimeout(() => {
      this.showBlock = true;
      this.startTimer();
    }, this.delay);
  },
  methods: {
    startTimer() {
      this.timer = setInterval(() => {
        this.reactionTime += 10;
      }, 10);
    },
    stopTimer(e) {
      clearInterval(this.timer);

      //Checking if the click was on the proper Box
      if (e.path[0].className === "box col-sm-3 green") {
        //transfering reactionTime to parent component using 'end' event
        console.log("this is correct");
        this.$emit("end", this.reactionTime);
        console.log("Rection time: " + this.reactionTime);
      } else {
        //Sending the Alert for the Wrong Answer
        console.log("This is not correct");
        this.$emit("wrong");
      }
    },
  },
};
</script>

<style>
ul {
  padding-left: 0px;
}

li {
  list-style-type: none;
}

.container-fluid {
  margin: 50px auto;
  max-width: 800px;
}
.container-fluid ul {
  padding: 0;
  margin: 0;
}

.box {
  height: 100px;
  margin: 20px;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-weight: 800;
  color: green;
}

.green {
  background-color: green;
  color: white;
}
</style>
