<template>
  <div>
    <h1>Reaction Timer <i class="fas fa-stopwatch"></i></h1>

    <button class="btn btn-success m-5" @click="start()" :disabled="isPlaying">
      Play
    </button>

    <Block
      v-if="isPlaying"
      :delay="delay"
      :boxNumber="boxNumber"
      @end="endGame"
      @wrong="wrongClick"
    />

    <Results v-if="showResult" :score="score" :wrongAnswer="wrongAnswer" />
    <button
      class="btn btn-success m-3"
      v-if="showAddToScoreboardButton"
      @click="inverseShowAddToScoreboardButton"
    >
      Add to Scoreboard
    </button>
    <Scoreboard
      :score="score"
      :showAddToScoreboardButton="showAddToScoreboardButton"
      :scores="scores"
      
    />
  </div>
</template>

<script>
import Block from "./components/Block.vue";
import Results from "./components/Results.vue";
import Scoreboard from "./components/Scoreboard.vue";

export default {
  name: "App",
  components: { Block, Results, Scoreboard },
  data() {
    return {
      isPlaying: false,
      delay: null,
      number: 10,
      score: null,
      showResult: false,
      showAddToScoreboardButton: false,
      boxNumber: null,
      wrongAnswer: false,
      scores:[],
    };
  },
  methods: {
    start() {
      this.delay = 1000 + Math.random() * 3000;
      this.boxNumber = Math.round(Math.random() * 9);
      this.isPlaying = true;
      this.showResult = false;
      this.wrongAnswer = false;
    },
    endGame(reactionTime) {
      this.score = reactionTime;
      this.isPlaying = false;
      this.showResult = true;
      this.showAddToScoreboardButton = true;
    },
    wrongClick() {
      this.wrongAnswer = true;
      this.isPlaying = false;
      this.showResult = true;
    },
    inverseShowAddToScoreboardButton() {
      this.showAddToScoreboardButton = false;
      let _date = new Date();
      let datee = _date.getDate()+'-'+(_date.getMonth() + 1)+'-'+_date.getFullYear()+' '+_date.getHours()+':'+_date.getMinutes()+':'+_date.getSeconds();

      this.scores.push({"date": datee, "score": this.score})

    },
  },
};
</script>

